<template>
  <div>
    <h3>Front End Networking and charting using the Star Wars API</h3>
    <p>
      Stats for the Star Wars movies from the Star Wars API (<a href="https://swapi.co" target="_blank">swapi.co</a>)
    </p>
    <div>
      How many
      <select @change="dataChanged($event)">
        <option value="characters">Characters</option>
        <option value="planets">Planets</option>
        <option value="starships">Star Ships</option>
        <option value="vehicles">Vehicles</option>
      </select>
      were involved in Star Wars?
    </div>
    <div class="chart-wrapper" v-if="displayChart">
      <SWChart :movieData="apiData" :currentlySelected="currentlySelected"></SWChart>
    </div>
    <div v-else>
      loading data...
    </div>
  </div>
</template>

<script>
//Need this for axios to work in Internet Explorer
import "@babel/polyfill";
  
import SWChart from './SWChart';

import axios from 'axios'

export default {
  name: "StarWarsApi",
  data(){
    return {
      apiData: {
        movies: [],
        characters: [],
        planets: [],
        starships: [],
        vehicles: []
      },
      currentlySelected: "characters",
      movieData: {
        movies: [], count: [], countTitle: ""
      },
      displayChart: false
    }
  },
  mounted(){
    this.getData();
  },
  methods: {
    getData(){
      
      this.displayChart = false;
      
      axios.get("https://swapi.co/api/films/")
        .then(response => {
          this.updateData(response.data.results);
        })
        .catch(e => {
          console.log(e);
        })
    },
    updateData(data){
      data.sort((a,b) => (a.episode_id > b.episode_id) ? 1 : -1);
      
      for(let i = 0; i < data.length; i++){
        this.apiData.movies.push(data[i].title);
        this.apiData.characters.push(data[i].characters.length);
        this.apiData.planets.push(data[i].planets.length);
        this.apiData.starships.push(data[i].starships.length);
        this.apiData.vehicles.push(data[i].vehicles.length);
      }
      
      this.displayChart = true;
    },
    dataChanged(event){
      this.currentlySelected = event.target.value;
    }
    
  },
  components: {
    SWChart
  }
};
</script>

<style scoped>
  @media only screen and (max-width: 600px) {
    select {
      width: 100%;
      text-align-last: center;
    }
    .chart-wrapper {
      width: 100%;
    }
  }
  @media only screen and (min-width: 600px){
    .chart-wrapper {
      width: 75%;
      border-radius: 10px;
    }
  }
  
  select {
    padding: 10px;
    border: none;
    border-radius: 4px;
    background-color: #f1f1f1;
  }
  
  .chart-wrapper {
    position: absolute;
    left: 0;
    right: 0;
    margin: 50px auto;
    background-color: black;
    background-image: url("../../assets/img/star_wars_bg.png");
  }

</style>



