<template>
  <div>
    <highcharts :options="chartOptions"></highcharts>
  </div>
</template>

<script>
  
import {Chart} from 'highcharts-vue';

export default {
  name: "SWChart",
  props: ["movieData", "currentlySelected"],
  data(){
    return {
      chartData: {},
      chartOptions: this.updateChart()
    }
  },
  watch: {
    currentlySelected(newVal){
      
      const newArr = [...this.movieData[newVal]];
      
      this.chartOptions.series[0].data = newArr;
      
      this.chartOptions.subtitle.text = "Number of " + newVal;
    }
  },
  methods: {
    updateChart(){
      const starWarsYellow = "#FFE91F";
      
      return {
        chart: {
          height: 300,
          spacing: [10,0,10,0],
          backgroundColor: 'rgba(0,0,0,0)',
          type: "column"
        },
        colors: [starWarsYellow],
        title: {
          text: ""
        },
        subtitle: {
          text: "Number of " + this.currentlySelected,
          style: {
            color: starWarsYellow
          }
        },
        tooltip: {
          enabled: false
        },
        legend: {
          enabled: false
        },
        xAxis: {
          categories: this.movieData.movies,
          labels: {
            style: {
              color: starWarsYellow
            }
          }
        },
        yAxis: {
          visible: false
        },
        plotOptions: {
          column: {
            dataLabels: {
              enabled: true,
              style: {
                color: starWarsYellow
              }
            },
            pointPadding: 0.2,
            borderWidth: 0
          }
        },
        series: [{
          data: [...this.movieData[this.currentlySelected]]
        }]
      }
    }
  },
  components: {
    highcharts: Chart 
  }
};
</script>

<style scoped>
  @media only screen and (max-width: 600px) {
    select {
      width: 100%;
      text-align-last: center;
    }
    .chart-wrapper {
      width: 100%;
    }
  }
  @media only screen and (min-width: 600px){
    .chart-wrapper {
      width: 75%;
      border-radius: 10px;
    }
  }
</style>



