<template>
  <div>
    <h3>Web Form validation</h3>
    <form method="post" @submit="formSubmit">
      <p v-if="errors.length">
        <b>Please correct the following error(s):</b>
        <ul>
          <li v-for="error in errors">{{ error }}</li>
        </ul>
      </p>

      <label for="frmNameA">Name</label>
      <input type="text" v-model="name" placeholder="Full name" required autocomplete="name">

      <label for="frmEmailA">Email</label>
      <input :class="{invalid: invalid_email}" type="email" v-model="email" placeholder="name@example.com" required autocomplete="email">

      <label for="frmEmailC">Confirm Email</label>
      <input :class="{invalid: invalid_email}" type="email" v-model="emailC" placeholder="name@example.com" required autocomplete="email">

      <label for="frmPhoneNumA">Phone</label>
      <input type="tel" v-model="phone" placeholder="+1-555-555-1212" required autocomplete="tel">

      <input type="submit" value="Submit">
    </form>
  </div>
</template>

<script>

export default {
  name: "WebForm",
  data(){
    return {
      errors: [],
      invalid_email: false,
      name: null,
      email: null,
      emailC: null,
      phone: null
    }
  },
  methods: {
    formSubmit(e){
      e.preventDefault();
      this.errors = [];
      this.invalid_email = false;
      if(this.email != this.emailC){
        this.errors.push('Email confirmation does not match')
        this.invalid_email = true;
      }
      if (!this.errors.length) {
        alert("No actual information was sent. This is for demonstration purposes.");
      }
    }
  }
};
</script>

<style scoped>
  @media only screen and (max-width: 500px) {

  }
  @media only screen and (min-width: 500px){

  }
  form {
    text-align: left;
  }
  input {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    transition: border-color 1s ease-out;
    outline: 0;
  }
  input:required {
    border-left: 2px solid palegreen;
  }
  input:invalid {
    border-left: 2px solid red;
  }
  input:focus:invalid {
    border: 2px solid red;
    border-radius: 4px;
  }
  ul {
    list-style-type: none;
    margin: 0;
    padding: 10px 0;
  }
  .invalid {
    background-color: #FAD1D1;
  }
</style>



