<template>
  <div>
    <h1>Interactive Samples</h1>
    <p>All samples are mobile friendly</p>

    <div class="sample-section">
      <GoogleTranslateBox />
    </div>
    <div class="sample-section">
      <StarWarsApi />
    </div>

  </div>
</template>

<script>
import GoogleTranslateBox from '../components/google_translate/GoogleTranslateBox'
import WebForm from '../components/WebForm'
import StarWarsApi from '../components/star_wars_api/StarWarsApi'
export default {
  name: "InteractiveSamples",
  data(){
    return {

    }
  },
  components: {
    GoogleTranslateBox, WebForm, StarWarsApi
  }
};
</script>

<style scoped>
  @media only screen and (max-width: 500px) {
    .sample-section {
      padding: 60px 0;
    }
  }
  @media only screen and (min-width: 500px){
    .sample-section {
      padding: 150px 0;
    }
  }
</style>



