<template>
  <div>
    <div v-show="image.url != ''">
      <div class="overlay" @click="closeModal()">
        <div class="contents">
          <div class="close_button">
            <button @click="closeModal()">close</button>
          </div>
          <div class="image_container">
            <img :src="image.url" alt="sample work with mock data">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: "PictureViewer",
  props: ['display_url'],
  data(){
    return {
      image: {
        url: ''
      }
    }
  },
  mounted(){
    this.image.url = this.display_url;
  },
  methods: {
    closeModal(){
      this.$emit('close-image-modal');
    }
  }
};
</script>

<style scoped>
@media only screen and (max-width: 480px) {
  .contents {
    width: 100%;
    max-height: 100%;
  }
}
@media only screen and (min-width: 480px) {
  .contents {
    width: 90%;
    max-height: 90%;
  }
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.1);
  display: flex;
  justify-content: center;
  align-items: center;
}
.contents {
  position: relative;
  padding: 10px;
  background-color: white;
  display: flex;
  flex-direction: column;
}
.image_container {
  width: 100%;
  height: 100%;
  padding: 20px 0;
  overflow-y: auto;
}
.image_container img {
  width: 100%;
}
.close_button {
  text-align: right;
}
.close_button button {
  padding: 12px;
  background-color: white;
  color: black;
  border: 2px solid darkblue;
  cursor: pointer;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
}
.close_button button:hover {
  background-color: darkblue;
  color: white;
}
</style>



