<template>
  <div class="menu-wrapper">

    <div class="menu-icon" @click="slideMenuIn()">
      <i class="fas fa-bars"></i>
    </div>
    <div class="menu-background" v-show="slide.in" @click="slideMenuOut()"></div>

    <div class="menu-body-wrapper" :class="{menu_slide_in: slide.in, menu_slide_out: slide.out}">
      <div>
        <ul>
          <li><router-link to="/about" @click.native="hideMenu">About</router-link></li>
          <li><router-link to="/projectexperience" @click.native="hideMenu">Project Experience</router-link></li>
          <li><router-link to="/interactivesamples" @click.native="hideMenu">Interactive Samples</router-link></li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Menu",
  data(){
    return {
      slide: {
        in: false, out: false
      }
    }
  },
  created(){
    window.addEventListener("resize", () => {
      this.menuHideHelper();
    });
  },
  methods: {
    menuHideHelper(){
      //Make sure the CSS animations are not applied when the window is greater than 800px
      if(window.innerWidth >= 500){
        //Instead of constantly setting these variables to false, see if they are true first
        if(this.slide.in || this.slide.out){
          this.slide.in = false;
          this.slide.out = false;
        }
      }
    },
    hideMenu(){
      //Hide the menu if it is in the Slide Out animation
      if(this.slide.in){
        this.slideMenuOut();
      }
    },
    slideMenuIn(){
      this.slide.in = true;
      this.slide.out = false;
    },
    slideMenuOut(){
      this.slide.out = true;
      this.slide.in = false;
    }
  }
};
</script>

<style scoped>
  @media only screen and (min-width: 500px) {
    .menu-icon {
      display: none;
    }
    .menu-wrapper {
      margin-bottom: 60px;
    }
    .menu-body-wrapper {
      /* background-color: #E2E2E2; */
      width: 100%;
      position: absolute;
      left: 0;
      top: 0;
    }
    .menu-body-wrapper div {
      margin: 0 5%;
    }
    ul {
      text-align: left;
    }
    ul li {
      display: inline-block;
    }
    a {
      padding: 20px;
    }
  }

  @media only screen and (max-width: 500px) {
    .menu-body-wrapper {
      position: fixed;
      left: 0;
      top: -100%;
      width: 100%;
      background-color: white;
    }
    .menu-wrapper {
      margin-bottom: 0;
    }
    ul {
      text-align: center;
    }
    ul li {
      display: block;
    }
    a {
      padding: 16px 0;
      width: 100%;
    }
  }

  @keyframes slide-in {
    from {top: -100%;}
    to {top: 0;}
  }
  @keyframes slide-out {
    from {top: 0;}
    to {top: -100%;}
  }

  a {
    display: block;
    text-decoration: none;
    color: darkblue;
  }
  a:hover {
    background-color: darkblue;
    color: white;
  }

  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }

  .router-link-active {
    background-color: darkblue;
    color: white;
  }

  .menu_slide_in {
    animation-name: slide-in;
    animation-duration: 0.5s;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
  }
  .menu_slide_out {
    animation-name: slide-out;
    animation-duration: 0.5s;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
  }


  .menu-background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.2);
  }

  .menu-icon {
    position: fixed;
    cursor: pointer;
    top: 0;
    left: 0;
    padding: 10px;
  }

</style>



