import Vue from "vue";
import Router from "vue-router";

import About from './views/About.vue';
import ProjectExperience from './views/ProjectExperience.vue';
import InteractiveSamples from './views/InteractiveSamples.vue';

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      redirect: { name: 'about' }
    },
    {
      path: "/about",
      name: "about",
      component: About
    },
    {
      path: "/projectexperience",
      name: "projectexperience",
      component: ProjectExperience
    },
    {
      path: "/interactivesamples",
      name: "interactivesamples",
      component: InteractiveSamples
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 };
  }
});



