<template>
  <div>
    <h1>Project Experience</h1>
    <p>Most recently worked as a Software Developer at an international bank</p>
    <div class="flexy">
      <div class="left">
        <h2>Dashboards</h2>
        <p>
          Developed multiple dashboards including the Front End UI / UX, Back End server / RESTful APIs, and database implementation.
          Built using HTML, CSS, JavaScript, VueJS, NodeJS, MongoDB, and AWS.
        </p>
      </div>
      <div>
        <figure>
          <picture>
            <a href="#" v-on:click.prevent="displayModal('dashboard')">
              <img :src="images.dashboards.thumbnail" alt="sample dashboard with mock data">
            </a>
          </picture>
          <figcaption><strong>Sample dashboard with mock data</strong></figcaption>
        </figure>
      </div>
    </div>
    <div class="flexy flexy-opposite">
      <div>
        <figure>
          <picture>
            <a href="#" v-on:click.prevent="displayModal('web tool')">
              <img :src="images.web_tools.thumbnail" alt="sample web tool with mock data">
            </a>
          </picture>
          <figcaption><strong>Sample web tool with mock data</strong></figcaption>
        </figure>
      </div>
      <div class="right">
        <h2>Web Tools</h2>
        <p>
          Developed multiple UI tools for daily task management, internal operations, and analytical analysis for multiple mobile apps.
          Built using HTML, CSS, JavaScript, VueJS, NodeJS, MongoDB, and AWS.
        </p>
      </div>
    </div>
    <div class="flexy">
      <div class="left">
        <h2>Mobile</h2>
        <p>
          Worked with a team of developers and designers to develop a hybrid mobile app.
        </p>
      </div>
      <div>
        <figure>
          <picture>
            <a href="https://tuyyo.love" target="_blank">
              <img :src="images.mobile.thumbnail" alt="Tuyyo Money Transfer app">
            </a>
          </picture>
          <figcaption><strong>Tuyyo Money Transfer app</strong></figcaption>
        </figure>
      </div>
    </div>
    <div v-if="modal_image.display">
      <PictureViewer v-on:close-image-modal="closeModal" :display_url="modal_image.url"/>
    </div>
  </div>
</template>

<script>
import PictureViewer from '../components/PictureViewer'
export default {
  name: "ProjectExperience",
  data(){
    return {
      modal_image: {
        display: false,
        url: ''
      },
      images: {
        dashboards: {
          thumbnail: 'https://s3.amazonaws.com/kov-img/img/dashboards/thumbnails/mk_internal_dashboard.png',
          full_size: 'https://s3.amazonaws.com/kov-img/img/dashboards/full_size/mk_internal_dashboard.png'
        },
        web_tools: {
          thumbnail: 'https://s3.amazonaws.com/kov-img/img/dashboards/thumbnails/mk_analytic_tools.png',
          full_size: 'https://s3.amazonaws.com/kov-img/img/dashboards/full_size/mk_analytic_tools.png'
        },
        mobile: {
          thumbnail: 'https://s3.amazonaws.com/kov-img/img/tuyyo/thumbnail/tuyyo.png'
        }
      }
    }
  },
  methods: {
    displayModal(image){
      this.modal_image.display = true;
      if(image === "dashboard"){
        this.modal_image.url = this.images.dashboards.full_size;
      } else if(image === "web tool"){
        this.modal_image.url = this.images.web_tools.full_size;
      }
    },
    closeModal(){
      this.modal_image.display = false;
    }
  },
  components: {
    PictureViewer
  }
};
</script>

<style scoped>
  @media only screen and (max-width: 500px) {
    .flexy {
      flex-direction: column;
      padding-bottom: 50px;
    }
    .flexy div {
      width: 100%;
    }
    .flexy-opposite {
      flex-direction: column-reverse;
    }
    .left {
      text-align: center;
    }
    .right {
      text-align: center;
    }
    img {
      width: 100%;
    }
  }
  @media only screen and (min-width: 500px){
    .flexy div {
      width: 50%;
      padding: 20px;
    }
    .left {
      text-align: left;
    }
    .right {
      text-align: right;
    }
    img {
      width: 100%;
    }
  }
  /* If the screen is larger than 800px */
  @media only screen and (min-width: 800px) {
    .flexy {
      flex-direction: row;
      padding: 100px 0;
    }
    img {
      width: 75%;
    }
  }
  .flexy {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 40px 0;
  }
  figure {
    margin: 0;
  }
  figcaption {
    padding: 10px 0;
  }
</style>



